<template>
  <div id="app">
    <audio src="./static/music.mp3" autoplay id="music" hidden>
    </audio>
    <span @click="control" class="control">音乐</span>
    <activity/>
  </div>
</template>

<script>
  import activity from './components/activity'

  export default {
    name: 'App',
    data: function () {
      return {
        audio: ''
      }
    },
    components: {
      activity
    },
    methods: {
      control: function () {
        if (!this.audio) {
          this.audio = document.getElementById('music');
        }
        if (this.audio !== null) {
          if (this.audio.paused) {
            this.audio.play();
          } else {
            this.audio.pause();
          }
        }
      }
    }
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }

  html, body, #app {
    width: 100%;
    height: 100%;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
    overflow: hidden;
  }

  .control {
    display: inline-block;
    font-size: 0.16rem;
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
    z-index: 3;
  }
</style>



