<template>
  <div class="activity-wrapper" id="activity-wrapper">
    <div class="page" @click="goStep(1)">介绍页面</div>
    <div v-for="item in question" :key="item.name" class="page" @click="goStep(item.step+1)">{{item.name}}</div>
    <div class="page">结尾页面</div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        question: [{
          name: '1',
          step: 1
        }, {
          name: '2',
          step: 2
        }, {
          name: '3',
          step: 3
        }]
      }
    },
    methods: {
      goStep: function (n) {
        $('#activity-wrapper').animate({top: `-${n * 100}%`});
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .activity-wrapper {
    position: absolute;
  }

  .activity-wrapper .page {
    height: 100vh;
  }
</style>



